.box{
    color:white;
    display: flex;
    font-size: 1rem;
    height: 10%;
    padding-top: 2%;
    padding-bottom: 2%;
    justify-content: center;
    align-items: center;
    margin-bottom: 2%;
    background-color:#222;
    box-shadow:0 5px 15px 0 rgba(0, 0, 0, 0.25);
}

.full{
    height:100vh;
    width:100vw;
}
.grid{
    transition: all 500ms ease-in-out;
    border: 1px solid black;
}


@keyframes an1{
    0%{
        background-color:green;
    }
    50%{
        background-color:red;

    }
}
@keyframes an2{
    0%{
        background-color:black;
        transform: scale(1,1);
    }
    50%{
        transform: scale(1.1,1.1);
        background-color:#2c1f1f;
    }
    100%{
        transform: scale(1.2,1.2);
        background-color: rgb(107, 107, 107);
    }
}
@keyframes an3{
    0%{
        background-color:red;
        transform: scale(1.5,1.5);
    }
    50%{
        transform: scale(1.1,1.1);
        background-color:#2c1f1f;
    }
    100%{
        transform: scale(1,1);
        background-color:black;
    }
}

.map{
    display: grid;
    justify-content: center;
    grid-template-rows:repeat(20,4%);
    grid-template-columns:repeat(20,4%);
    height: 80%;
    transition: all 500ms ease-in-out;
}
.wor{
    padding: 1%;
    user-select:none;
    font-weight: 700;
    transition:0.1s;
}
#st:hover{
    color: tomato;
}
#end:hover{
    color: tomato;
}
#wall:hover{
    color: tomato;
}
#vis:hover{
    color: tomato;
}